@import "../../scss/colors.scss";
@import "../../scss/reset.scss";

@keyframes shadow-pulse {
  0% {
    box-shadow: 0 0 0 0px rgba(0, 112, 244, 0.4);
  }
  100% {
    box-shadow: 0 0 7px 25px rgba(0, 112, 244, 0);
  }
}

#earny {
  #activate-cashback.canvas {
    display: flex;
    overflow: hidden;

    .left-logo {
      svg {
        height: 100%;
        width: 120px;
      }
    }

    .center {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 20px 0 15px 0;
      margin-right: 15px;

      .retailer-name {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-bottom: 15px;

        img {
          width: 20px;
          height: 20px;
          margin-right: 6px;
        }

        span {
          font-size: 20px;
          font-weight: bold;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          text-align: center;
          color: #232e3b;
        }
      }

      button#activate-cashback-button {
        width: 100%;
        height: 50px;
        border-radius: 25px;
        background-color: #0070f4;
        font-family: Montserrat;
        font-size: 18px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff !important;
        padding: 16px 22px;
        animation: shadow-pulse 1.5s infinite;
        margin-bottom: 12px;
      }

      .activate-double-cashback {
        button#activate-double-cashback-button {
          text-decoration: underline !important;
          width: 100%;
          color: #526173;
          font-family: Montserrat;
          font-size: 16px;
          font-weight: 500;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          text-align: center;
          background: none;
          height: unset;
        }
      }
    }

    #close-button-right {
      width: 45px;
      background-color: $lightGray;
      height: unset;
    }
  }
}
