#earny {
  .button-bar-two {
    display: flex;
    position: absolute;
    left: 0px;
    right: 0px;

    button {
      height: 50px;
      width: 50%;

      &.left {
        min-width: 0px !important;
        margin-left: 0;
        margin-right: 8px !important;
      }

      &.right {
        min-width: 0px !important;
        margin-left: 8px !important;
        margin-right: 0;
      }
    }
  }

  button a {
    display: inline-block;
    width: 100%;
    text-decoration: none !important;
  }
}
