@import "../../scss/colors.scss";

#coupon-modal {
  position: relative;
  background-color: white;
  width: 600px;
  min-height: 420px;
  height: 420px;
  max-height: 420px;
  z-index: 10001;
  border-radius: 6px;
  box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.2);

  #earny-logo {
    width: 72px;
    position: absolute;
    z-index: 1;
    top: 16px;
    left: 19px;
  }

  #close-button {
    cursor: pointer;
    z-index: 1;
    background-color: transparent !important;
    padding: 0;
    position: absolute;
    top: 20px;
    right: 20px;
    svg {
      height: 16px;
      width: 16px;
      path {
        fill: $lightGray;
      }
    }
  }

  .coupon-checking {
    display: flex;
    flex-direction: column;
    align-items: center;

    h3 {
      font-family: Montserrat;
      font-size: 25px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      color: #232e3b;
      margin: 0 0 40px 0;
    }
  }

  .coupon-complete {
    display: flex;
    flex-direction: column;
    align-items: center;

    h3 {
      margin-top: 82px;
      margin-bottom: 20px;
      font-family: Montserrat;
      font-size: 25px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      color: #232e3b;
    }

    p {
      width: 324px;
      font-family: Montserrat;
      font-size: 16px;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      text-align: center;
      color: #526173;
    }

    .cashback {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 30px 0;
      width: 300px;
      height: 50px;
      border-radius: 6px;
      background-color: #e6fffa;
      padding: 20px;

      div {
        font-family: Montserrat;
        font-size: 14px;
        font-weight: 600;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        color: #19ceac;

        &.title {
          font-size: 14px;
          line-height: 1.71;
        }

        &.value {
          font-size: 16px;
          line-height: 1.5;
        }
      }
    }

    button.cta {
      width: 300px;
      height: 50px;
      border-radius: 6px;
      font-family: Montserrat;
      font-size: 16px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff !important;
      background-color: #0070f4;
    }
  }
}