@import "../../scss/colors.scss";

#confirmation-modal {
  display: block;
  position: absolute;
  z-index: 1;
  left: 0;
  width: 100%;
  height: 190px;
  padding-left: 20px;
  padding-right: 20px;
  border: none;
  opacity: 0;
  border-radius: 6px;
  background-color: transparent;
  transition: visibility 0.3s, opacity 0.3s, transform 0.3s;

  &.fadeOut {
    visibility: hidden;
    opacity: 0;
    transform: translate(0, 20px);
  }

  &.fadeIn {
    opacity: 1;
    transform: translate(0, -20px);
  }

  .modal-content {
    padding: 20px;
    position: relative;
    border: none;
    height: 100%;
    background-color: #fefefe;
    box-shadow: 0 0 30px -5px rgba(177, 186, 197, 0.5);
  }

  #close {
    padding: 0;
    margin: 0;
    border: none;
    position: absolute;
    top: 20px;
    right: 20px;
    svg {
      height: 16px;
      width: 16px;
      path {
        fill: $dark;
      }
    }

    &:focus {
      outline: none;
    }
  }

  .close:hover,
  .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }

  .modal-price-drop {
    display: flex;

    svg {
      min-width: 70px;
      height: 70px;
      width: 70px;
      margin-right: 15px;
    }

    .price-drop-notice {
      width: 100%;
      padding-right: 20px;

      p {
        line-height: 24px;
        font-weight: 100;
        font-family: Montserrat;
        font-size: 16px;
        font-stretch: normal;
        text-align: left;
      }
    }
  }

  .download-links {
    position: absolute;
    bottom: 20px;
    width: calc(100% - 40px);
    min-height: 45px;
  }

  .store-button {
    position: absolute;
    width: 47%;
    color: #000000;
    cursor: pointer;
    border: none;
    &:focus {
      outline: none;
      border: none !important;
    }
  }

  #app-store-button {
    left: 0;
  }

  #android-store-button {
    right: 0;
  }
}
