@import "../../scss/colors.scss";

#earny {
  #cashback-activated.canvas {
    overflow: hidden;

    button#close-button svg path {
      stroke: white;
    }

    .earny-logo img {
      min-height: 35px;
      width: 80px;
      height: auto;
      margin-top: 15px;
      margin-bottom: 15px;
    }
    .cashback-activated-text {
      height: 60px;
      align-items: center;
      justify-content: center;
      display: flex;
      background-color: #ea9600;
      font-family: Montserrat;
      font-size: 18px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff;
    }

    .cashback-exclusion-text {
      height: 60px;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-left: 25px;
      padding-right: 25px;

      svg {
        height: 23px;
        width: 23px;
        min-width: 23px;
        margin-right: 9px;
      }

      p {
        font-family: Montserrat;
        font-size: 17px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.41;
        letter-spacing: normal;
        color: $dark;
      }
    }
  }
}
