@import "../../scss/colors.scss";

#earny-widget-root {
  #product-details-modal.canvas {
    position: relative;
    // overflow-y: auto;

    #close-button {
      cursor: pointer;
      line-height: 16px;
      position: absolute;
      top: 20px;
      right: 20px;
      svg {
        height: 16px;
        width: 16px;
        path {
          fill: $dark;
        }
      }
    }

    &::-webkit-scrollbar {
      width: 15px;
    }

    &::-webkit-scrollbar-track {
      margin-top: 20px;
      background: transparent;
      border-radius: 5px;
      height: 100px;
    }

    &::-webkit-scrollbar-thumb {
      box-shadow: inset 0 0 10px 10px #d1dce7;
      border-left: solid 5px transparent;
      border-right: solid 5px transparent;
      height: 390px;
      border-radius: 5px;
    }

    .product-details {
      min-height: 80px;
      display: flex;

      img {
        object-fit: contain;
        width: 80px !important;
        height: 80px;
        min-width: 80px;
      }

      .product-content {
        max-width: 300px;
        margin-left: 15px;
        text-align: left;

        .product-name {
          padding-right: 20px;

          p {
            font-family: Montserrat;
            font-size: 16px;
            font-weight: 600;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.38;
            color: $dark;
            padding: 0;
            margin: 0;
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
          }
        }
        .product-prices {
          margin-top: 4px;
          display: flex;

          .product-new-price {
            margin-right: 12px;
            font-size: 18px;
            font-weight: 500;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.33;
            letter-spacing: normal;
            color: #ee0000;
          }

          button#save-button {
            margin-top: 2px;
            width: 23px;
            height: 20px;
            outline: none;
            border: none;
            padding: 0;
            line-height: 0;
            background-color: none;;

            &.saved {
              path#save-path {
                // stroke: none;
                stroke: #ff6060;
                // stroke-width: 1px;
                fill: #ff6060;
              }
            }
          }
        }
      }
    }

    .product-time-to-buy {
      margin-top: 15px;
      display: flex;
      height: 60px;
      box-shadow: 0 5px 20px -5px rgba(177, 186, 197, 0.25);

      span {
        font-family: Montserrat;
        font-size: 14px;
        font-weight: 500;
        line-height: 58px;

        &.left {
          margin-left: 16px;
          margin-right: 17px;
        }
        &.right {
          margin-left: 17px;
          margin-right: 16px;
        }
      }
    }

    #price-stats {
      margin-top: 10px;
      display: flex;
    }

    .price-stat {
      display: flex;
      flex-direction: column;
      width: 50%;
      padding: 11px 0 11px 0;
      height: 60px;
      box-shadow: 0 5px 20px -5px rgba(177, 186, 197, 0.25);

      &.left {
        margin-right: 5px;
      }

      &.right {
        margin-left: 5px;
      }

      p {
        font-family: Montserrat;
        font-size: 12px;
        font-weight: 600;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        color: #b1bac5;
        padding: 0;
        margin-left: auto;
        margin-right: auto;
      }

      .price-stat-price {
        font-weight: 600;
        margin-top: 5px;
        color: $dark;
        font-family: Montserrat;
        font-size: 14.5px;
      }
    }

    .recharts-wrapper {
      margin: 0 auto;
    }

    .price-tracking-earny-logo img {
      min-height: 43px;
      width: 100px;
      height: auto;
      margin-bottom: 15px;
    }

    .pdp-no-product {
      margin-top: 58px;

      img {
        width: 154px;
      }
    }

    .pdp-no-product-description {
      margin-top: 20px;
      margin-left: 48px;
      margin-right: 48px;

      p {
        font-family: Montserrat;
        font-size: 22px;
        font-weight: 600;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.27;
        color: $dark;
      }
    }
  }
}
