@import "../../scss/colors.scss";

#earny-widget-root {
  #product-details-pending-modal.canvas {
    position: relative;
    text-align: center;

    #close-button {
      cursor: pointer;
      line-height: 16px;
      position: absolute;
      top: 20px;
      right: 20px;
      svg {
        height: 16px;
        width: 16px;
        path {
          fill: $dark;
        }
      }
    }

    svg {
      height: 120px;
      width: 120px;
    }

    p.loading-product-details-title {
      margin-top: 20px;
      font-size: 22px;
      font-weight: 500;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.27;
      letter-spacing: normal;
      text-align: center;
      color: $dark;
    }

    p.information-title {
      margin-top: 10px;
      font-family: Montserrat;
      font-size: 16px;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      text-align: center;
      color: $dark;
    }
  }
}
