@import "../scss/fonts.scss";
@import "../scss/colors.scss";
@import "../scss/reset.scss";

#earny-root {
  #earny {
    top: 0;
    right: 0;
    background-color: transparent;
    z-index: 2147483647 !important;
    overflow: hidden;
    font-size: 100%;
    font-style: normal !important;
    box-sizing: content-box !important;
    text-rendering: geometricPrecision;
    position: fixed;

    #earny-box {
      padding: 20px 20px 25px 20px;
    }

    .canvas {
      position: relative;
      background: #fff;
      box-sizing: border-box !important;
      text-align: center;
      border-radius: 6px;
      box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.5);

      #close-button {
        cursor: pointer;
        z-index: 1;
        background-color: transparent !important;
        padding: 0;
        position: absolute;
        top: 20px;
        right: 20px;
        svg {
          height: 16px;
          width: 16px;
          path {
            fill: $lightGray;
          }
        }
      }
    }
  }

  #fullscreen-modal {
    position: fixed;
    width: 600px;
    left: calc(50vw - 300px);
    top: calc(50vh - 230px);
    z-index: 10000;

    #background {
      position: fixed;
      top: 0px;
      left: 0px;
      background-color: rgb(80, 80, 80);
      opacity: 0.8;
      height: 100vh;
      width: 100vw;
    }
  }
}