@import "../../scss/colors.scss";

#checkout-modal.canvas {
  width: 380px;
  display: flex;
  flex-direction: column;
  align-items: center;

  #earny-logo {
    width: 72px;
    position: absolute;
    z-index: 1;
    top: 16px;
    left: 19px;
  }

  h3 {
    font-size: 24px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: $dark;
    margin-bottom: 20px;
  }

  button.cta {
    width: 300px;
    height: 50px;
    border-radius: 6px;
    background-color: #0070f4;
    margin-bottom: 40px;
    font-size: 16px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 50px;
    letter-spacing: normal;
    text-align: center;
    color: #ffffff !important;
  }
}
