@import "../scss/reset.scss";
@import "../scss/fonts.scss"; // Preload fonts
@import "../scss/buttons.scss";
@import "../scss/colors.scss";

#earny-widget-root {
  z-index: 2147483646 !important;
  display: block;
  position: fixed;
  top: 200px;
  right: 20px;
  left: auto !important;
  text-rendering: geometricPrecision;

  .recharts-tooltip-wrapper {
    font-family: "Montserrat";
  }

  .recharts-default-tooltip {
    border: none;
    border-radius: 3px;
    box-shadow: 0px 8px 20px -5px rgba(177, 186, 197, 0.5);
  }

  #widget {
    #button-wrapper {
      display: flex;
      justify-content: flex-end;

      #button-wrapper-right {
        #widget-button {
          cursor: pointer;
          overflow: hidden;
          border: solid 1px #e7e7e7;
          display: flex;
          height: 40px;
          position: relative;
          border-radius: 6px;
          box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.05);
          background-color: #ffffff;

          .logo {
            margin-left: 13px;
            margin-right: 10px;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 54px;
            svg {
              max-width: 54px;
            }
          }

          #banner {
            transition: width 0.3s;
            display: flex;
            justify-content: center;
            align-items: center;
            opacity: 1;
            background-color: $blue;
            position: relative;
            right: 0;
            width: 7px;
            height: 100%;

            svg {
              transition: opacity 0.6s;
              opacity: 0;
            }
          }
        }
      }
    }

    #content-wrapper {
      overflow: hidden;
      border-radius: 5px;
      border: solid 1px #e7e7e7;
      box-shadow: 0 10px 20px -5px rgba(177, 186, 197, 0.25);
      background-color: #ffffff;
      margin-top: 20px;
      transition: visibility 0.1s, opacity 0.1s;
      opacity: 0;

      &.earny-hide {
        visibility: hidden;
        opacity: 0;
      }

      &.earny-show {
        visibility: visible;
        opacity: 1;
      }
    }
  }
}
