@import "../../scss/colors.scss";

#earny-widget-root {
  #no-product-details-page.canvas {
    position: relative;

    #close-button {
      line-height: 16px;
      position: absolute;
      top: 20px;
      right: 20px;
      svg {
        height: 16px;
        width: 16px;
        path {
          fill: $dark;
        }
      }
    }

    p.add-to-view-details-title {
      font-family: Montserrat;
      font-size: 22px;
      font-weight: 600;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.27;
      letter-spacing: normal;
      text-align: center;
      color: $dark;
    }

    p.information-title {
      padding: 0 60px 0 60px;
      margin-top: 10px;
      font-family: Montserrat;
      font-size: 16px;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      text-align: center;
      color: $dark;
    }

    button {
      width: 100%;
      height: 60px;
      font-family: Montserrat;
      font-size: 18px;
      font-weight: 500;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      border-radius: 6px;
    }

    button#add-to-watchlist {
      color: #ffffff !important;
      margin-top: 25px;
      background-color: $blue;
    }

    button#activate-cashback {
      margin-top: 10px;
      color: $blue !important;
      border-radius: 6px;
      border: solid 1px #0070f4 !important;
      background-color: transparent;
    }
  }
}
