@import "../../scss/buttons.scss";
@import "../../scss/colors.scss";

#earny-widget-root {
  #login-modal.canvas {
    position: relative;

    button {
      border-radius: 5px !important;
    }

    #close-button {
      cursor: pointer;
      line-height: 16px;
      position: absolute;
      top: 28px;
      right: 30px;
      svg {
        height: 16px;
        width: 16px;
        path {
          fill: $dark;
        }
      }
    }

    #login-modal-content {
      padding: 20px 30px 30px 30px;

      p.watchlist-title {
        font-family: Montserrat;
        font-size: 22px;
        font-weight: 600;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.27;
        letter-spacing: normal;
        text-align: left;
        color: $dark;
      }

      p.watchlist-extra {
        margin-top: 10px;
        font-family: Montserrat;
        font-size: 16px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.5;
        letter-spacing: normal;
        text-align: left;
        color: $dark;
      }

      #login-buttons {
        margin-top: 20px;
        display: flex;

        button {
          height: 50px;
          width: 50%;

          &.left {
            min-width: 0px !important;
            margin-left: 0;
            margin-right: 8px !important;
          }

          &.right {
            min-width: 0px !important;
            margin-left: 8px !important;
            margin-right: 0;
          }
        }
      }

      #signin-button {
        border: none !important;
        background-color: $blue !important;
        color: white !important;
        font-family: "Montserrat", sans-serif;
        font-size: 16px;
        font-weight: bold;
      }

      #signup-button {
        border: none !important;
        background-color: $lightGray !important;
        color: $dark !important;
        font-family: "Montserrat", sans-serif;
        font-size: 16px;
        font-weight: bold;
      }
    }
  }
}
