@import "../../scss/colors.scss";

#earny-widget-root {
  #startup-screen-modal.canvas {
    p.drag-it-title {
      margin: 25px 45px 0 45px;
      font-family: Montserrat;
      font-size: 22px;
      font-weight: 600;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.27;
      letter-spacing: normal;
      text-align: center;
      color: $dark;
    }

    p.description {
      margin: 10px 45px 0 45px;
      font-family: Montserrat;
      font-size: 16px;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      text-align: center;
      color: $dark;
    }

    button#confirm-button {
      margin-top: 25px;
      width: 100%;
      height: 60px;
      border-radius: 6px;
      background-color: $blue;
      color: white !important;
      font-family: Montserrat;
      font-size: 18px;
      font-weight: bold;
    }
  }
}
